.adcmdr-btn-group {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 0.75rem;
}
.adcmdr-btn-group--center {
  align-items: center;
}

.adcmdr-ob-notice {
  background: #190933;
  background: linear-gradient(to bottom right, #190933 50%, #0e051d 60%);
  border-left: none;
  border: none;
  border-radius: 4px;
  color: #fff;
  padding: 1.5rem;
  margin-top: 1.5rem;
}
.adcmdr-ob-notice h2,
.adcmdr-ob-notice h3 {
  color: #fff;
  margin-top: 0;
}
.adcmdr-ob-notice a:not(.button) {
  color: #fff;
  transition: opacity 250ms ease-in-out;
}
.adcmdr-ob-notice a:not(.button):hover {
  opacity: 0.75;
}
.adcmdr-ob-notice ul {
  margin-top: 0.75rem;
}
.adcmdr-ob-notice .adcmdr-logo {
  width: 200px;
  height: auto;
  max-width: 100%;
}

.wrap .adcmdr-ob-notice {
  margin-top: 1.5rem;
}

.adcmdr-ob-row {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
@media only screen and (min-width: 783px) {
  .adcmdr-ob-row {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .adcmdr-ob-row > .adcmdr-ob-col {
    flex: 1;
  }
  .adcmdr-ob-row > .adcmdr-ob-col p {
    max-width: 400px;
  }
}

.adcmdr-ob-intro {
  border-bottom: 1px solid rgba(195, 196, 199, 0.25);
  padding-bottom: 1.5rem;
  margin-bottom: 1.5rem;
}
@media only screen and (min-width: 783px) {
  .adcmdr-ob-intro {
    align-items: center;
  }
}
@media only screen and (min-width: 783px) {
  .adcmdr-ob-intro .adcmdr-ob-dismiss {
    margin-left: auto;
  }
}

.adcmdr-ob-notice--published .adcmdr-ob-intro h4 {
  margin: 0;
}

#adminmenu #toplevel_page_ad-commander div.wp-menu-image {
  background: url(../img/icon.svg) no-repeat center center;
  background-size: 18px auto;
}
#adminmenu #toplevel_page_ad-commander:hover div.wp-menu-image {
  background-image: url(../img/icon-hover.svg);
}
#adminmenu #toplevel_page_ad-commander.wp-menu-open div.wp-menu-image {
  background-image: url(../img/icon-active.svg);
}